.promo {
  grid-area: advertising;
}

.promo__list {
  margin: 0;
  padding: 0;
  display: flex;
  flex-direction: column;
  gap: 20px;
  align-items: stretch;
}

.promo__list {
  list-style: none;
}

.promo__item {
  position: relative;
  width: 100%;
}

.promo__title {
  margin: 0;
  font-weight: 600;
  font-size: 18px;
  line-height: 26px;
  color: #FFFFFF;
  /* max-width: 212px; */
  position: absolute;
  bottom: 24px;
  left: 24px;
}

.promo__text {
  margin: 0;
  padding: 2px 8px;
  font-weight: 400;
  font-size: 12px;
  line-height: 18px;
  color: #FFFFFF;
  position: absolute;
  top: 5%;
  right: 5%;
  background: rgba(255, 255, 255, 0.2);
  border-radius: 8px;
}

.promo__subtitle {
  margin-top: 20px;
  margin-bottom: 0;
  font-weight: 400;
  font-size: 12px;
  line-height: 18px;
  color: #828D9E;
  text-align: center;
}

.promo__image {
  width: 100%;
}

@media screen and (max-width: 1110px) {
  .promo__title {
    bottom: 16px;
    left: 16px;
  }
}

@media screen and (max-width: 992px) {
  .promo {
    grid-column-start: 1;
    grid-column-end: 2;
    grid-area: auto;
  }

  .promo__image {
    max-height: 200px;
  }
}

@media screen and (max-width: 768px) {
  .promo__subtitle {
    display: none;
  }
}