.input {
  margin: 0;
  padding: 12px 16px;
  border: 1px solid #E2E5EA;
  border-radius: 16px;
  background-color: #FFFFFF;
  font-weight: 500;
  font-size: 16px;
  line-height: 24px;
  color: #303030;
}

.input::placeholder {
  color: #999999;
}

.input:focus {
  outline: none;
  border: 1px solid #717171;
}

.input__title {
  display: flex;
  flex-direction: column;
  gap: 8px;
  font-weight: 500;
  font-size: 14px;
  line-height: 20px;
  color: #303030;
  width: 100%;
}

.input__title_limited_width {
  max-width: 400px
}

.input__title:focus-visible {
  border: 1px solid #717171;
  outline: none;
}