.questionary__text-areas {
  margin-top: 16px;
  margin-bottom: 36px;
  display: flex;
  justify-content: space-between;
  gap: 16px;
}

.questionary__label_step_third {
  display: flex;
  flex-direction: column;
  gap: 8px;
  width: 100%;
}

.questionary__input_step_third {
  min-height: 98px;
  padding: 14px 16px;
  border: 1px solid #E2E5EA;
  border-radius: 16px;
  font-weight: 500;
  font-size: 16px;
  line-height: 24px;
  color: #303030;
}

.questionary__input_step_third::placeholder {
  font-weight: 500;
  font-size: 16px;
  line-height: 24px;
  color: #999999;
}

.questionary__input_step_third:focus {
  outline: none;
  border: 1px solid #717171;
}

.questionary__container_attitude {
  display: grid;
  gap: 16px;
  grid-template-columns: repeat(3, 1fr);
}

.questionary__container_interests {
  display: grid;
  gap: 16px;
  grid-template-columns: repeat(4, 1fr);
}

.questionary__min-text {
  margin: 0;
  font-weight: 400;
  font-size: 12px;
  line-height: 18px;
  color: #999999;
}

@media screen and (max-width: 992px) {
  .questionary__text-areas {
    flex-direction: column;
  }

  .questionary__text-areas {

  }
  
  .questionary__label_step_third {
    gap: 4px;
  }

  .questionary__container_attitude {
    display: flex;
  }
  
  .questionary__container_interests {
    display: flex;
  }
}