.edit-profile__title {
  margin-top: 0;
  margin-bottom: 24px;
  font-weight: 600;
  font-size: 24px;
  line-height: 30px;
  color: #242A33;
}

.edit-profile__form {
  padding: 24px;
  background: #FFFFFF;
  box-shadow: 2px 5px 15px rgba(26, 31, 39, 0.02), 10px 15px 40px rgba(26, 31, 39, 0.03);
  border-radius: 16px;
  display: flex;
  flex-direction: column;
}

.edit-profile__button {
  margin-top: 24px;
  margin-bottom: 24px;
  border: 0;
  padding: 0;
  background-color: transparent;
  display: flex;
  gap: 8px;
  align-items: center;
  cursor: pointer;
}

