  margin-top: 0;
} */
/* 
.edit-profile__button_filter-open {
  padding-bottom: 24px;
  border-bottom: 1px solid #D9DCE1;
} */

.edit-profile__subtitle {
  margin: 0;
  font-weight: 600;
  font-size: 18px;
  line-height: 26px;
  color: #303030;
}

.edit-profile__button-arrow {
  width: 16px;
  height: 16px;
  background-repeat: no-repeat;
  transform: translateY(25%);
  background-image: url(../../images/arrow-right.svg);
}

.edit-profile__button-arrow_filter-open {
  background-image: url(../../images/arrow-bottom.svg);
}

.edit-profile__container {
  border-bottom: 1px solid #D9DCE1;
}

.edit-profile__choose-theme, .edit-profile__choose-type {
  margin-top: 0;
  margin-bottom: 24px;
  font-weight: 500;
  font-size: 16px;
  line-height: 24px;
  color: #303030;
}

.edit-profile__save-button {
  padding: 14px 77.5px;
  border: none;
  background: #6C26FF;
  box-shadow: 0px 15px 30px rgba(108, 38, 255, 0.15);
  border-radius: 16px;
  font-weight: 500;
  font-size: 16px;
  line-height: 24px;
  color: #FFFFFF;
  align-self: flex-end;
}

.edit-profile__label {
  width: 100%;
}

.edit-profile__radio-input {
  margin-bottom: 24px;
  display: flex;
  gap: 16px;
}

.edit-profile__input_invisible {
  position: absolute;
  width: 1px;
  height: 1px;
  overflow: hidden;
  clip: rect(0 0 0 0);
}

.edit-profile__input_visible {
  display: grid;
  grid-template-rows: auto auto 1fr;
  padding: 24px;
  cursor: pointer;
  height: calc(100% - 48px);
}

.edit-profile__input_invisible:checked + .edit-profile__input_visible {
  background: linear-gradient(0deg, rgba(108, 38, 255, 0.03), rgba(108, 38, 255, 0.03)), #FFFFFF;
  border: 2px solid #6C26FF;
border-radius: 12px;
}

.edit-profile__input_invisible:not(:checked) + .edit-profile__input_visible {
  background: #FFFFFF;
  border: 1px solid #A6ADB8;
  border-radius: 12px;
}

.edit-profile__input-title {
  margin-top: 0;
  margin-bottom: 8px;
  font-weight: 600;
  font-size: 18px;
  line-height: 26px;
  color: #242A33;
  display: flex;
  gap: 12px;
  align-items: center;
}

.edit-profile__input-tick {
  width: 14px;
  height: 14px;
  background-position: center;
  background-repeat: no-repeat;
  background-image: url(../../images/verification-tick.svg);
}

.edit-profile__input-advantages {
  padding: 0;
  margin: 0;
  margin-bottom: 24px;
  display: flex;
  flex-direction: column;
  gap: 8px;
}

.edit-profile__input-advantage {
  list-style: none;
  display: grid;
  grid-template-columns: auto 1fr;
  gap: 8px;
}

.edit-profile__input-icon {
  width: 16px;
  height: 16px;
  background-position: center;
  background-repeat: no-repeat;
  background-image: url(../../images/green-tick.svg);
}

.edit-profile__input-text {
  margin: 0;
  font-weight: 400;
  font-size: 14px;
  line-height: 20px;
  color: #636870;
}

.edit-profile__input-price {
  display: flex;
  justify-content: space-between;
  align-self: flex-end;
}

.edit-profile__input-price-value {
  margin: 0;
  font-weight: 500;
  font-size: 14px;
  line-height: 20px;
  color: #FF4F9F;
}

.edit-profile__input-rate {
  margin: 0;
  font-weight: 400;
  font-size: 12px;
  line-height: 18px;
  color: #A6ADB8;
}

@media screen and (max-width: 992px) {
  .edit-profile__radio-input {
    flex-direction: column;
  }

  .edit-profile__save-button {
    align-self: auto;
    font-size: 14px;
    line-height: 20px;
  }
}

@media screen and (max-width: 762px) {
  .edit-profile__input_visible {
    padding: 16px;
  }
}